import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, WindowComponent } from 'summer';
import { CLASS_NAME, OrigemMercadoria } from '../../../../models/dtos/ger/origemMercadoria';

interface OrigemMercadoriaEditViewProps extends EditViewProps<OrigemMercadoria> {}

const OrigemMercadoriaEditView: FC<OrigemMercadoriaEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);

  const formProps = useForm<OrigemMercadoria>({ defaultValues: selectedItem, mode: 'onBlur' });

  const defaultWidth: number = 100;

  return (
    <WindowComponent title="Manutenção de Origem de Mercadoria" wikiId={2544}>
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, maxLength: 1 } }}
              id="codigo"
              inputProps={{ autoFocus: true }}
              isPrependLabel={true}
              label="Código"
              labelWidth={defaultWidth}
              placeholder="Informe o Código."
            />
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, maxLength: 60 } }}
              id="descricao"
              isPrependLabel={true}
              label="Descrição"
              labelWidth={defaultWidth}
              placeholder="Informe a Descrição"
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default OrigemMercadoriaEditView;
